.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  padding-top: 60px;
  overflow: auto;
}

.grid-item {
  margin: 5px;
  
  padding: 10px;
}

@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr 1fr;
  }
}


.container{
  max-width: 100%;
  padding: 0;
}

.product-parent-div{
 width: 250px;
 height: 400px;
 background: #61dafb;
 text-align: center;
 padding: 5px;
 box-shadow: #282c34;
}

/* .alice-carousel__wrapper{
  
  height: 250px !important;
} */

.top-bar{
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #f1f1f1;
  z-index: 999;

}

.alice-carousel__dots{
  margin: 3px !important;
  padding: 3px;
}

.image-slide{
  padding: 0.3%;
  width: 100%;
  margin: 1%;
  
}

label[data-shrink="false"] + .MuiInputBase-formControl .css-1t8l2tu-MuiInputBase-input-MuiOutlinedInput-input{
  height: auto;
}

.cart-image{
  height: 100px;
  width: 100px;
  background-color: white;
  object-fit: contain;
  margin: 0 0 3px 0 ;
}

.btn-grid-parent{
  display: grid;
  grid-template-columns: 20px 20px;
}

.cart-item-parent{
  display: grid;
  grid-template-columns: 20% 80%;
  height: 160px;
  background-color: #61dafb;
  margin: 0.3%;
  padding-left: 2%;
  padding: 1% 1% 1% 2%;
}

.price-grid{
  display: grid;
  grid-template-columns: 80px 80px 80px;
  text-align: left;
}

.cart-item-child-1{
  text-align: left;
  padding-left: 10px;
}

.order-parent{
  display: grid;
  grid-template-columns: 1fr 2fr;
  justify-items: left;
  align-items: left;
}


.containt_main {
  display: flex;
  background-color: #2874f0;
  align-items: center;
  padding: 1%;
  width: 100%;
  margin-left: 1%;
}

.order-child{
  justify-self: left;
  align-self: center;
}

.cart-item{
  width: 90%;
  background-color: antiquewhite;
  height: 20vh;
  margin: 0.3%;
  padding: 2%;
}

.address-parent{
  display: grid;
  width: 100%;
  background-color: beige;
  height: 50px;
  margin: 0.3%;
  padding-left: 2%;
  grid-template-rows: auto auto;
}

.address-child{
  padding: 5px 0 0 0 ;
  line-height: 2;
}

.address-change-btn{
  grid-row: 1/2;
}

.order-parent{
  width: 90%;
  height: min-content;
  background-color: bisque;
  color: #390000;
  text-align: left;
  padding: 3%;
  margin: 2%;
  justify-self: center;
  align-self: center;
  
  
}




.align-parent{
  display: grid;
  grid-template-columns: auto auto auto;
}

.padding_10{
  color: white;
}

.header-section{
  font-family: 'Oswald', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
